<template>
  <div class="home">
    <div class="container mt-4">
      <div class="row">
        <!-- right nav -->
        <NavRight />
        <v-card class="col-md-6 pa-4 ">
          <h3 class="col-md-12">
            <i class="mdi mdi-video"></i>
            إضافة فيديو جديد
          </h3>
          <v-divider></v-divider>
          <div>
            <div v-if="!showProg">
              <v-col cols="12" sm="12">
                <v-text-field
                  v-model="title"
                  label="عنوان الفيديو"
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12">
                <v-textarea
                  label="وصف الفيديو"
                  auto-grow
                  v-model="desc"
                  outlined
                  rows="3"
                  row-height="25"
                  shaped
                ></v-textarea>
              </v-col>
              <v-col cols="12" sm="12">
                <v-text-field
                  label=" الفيديو"
                  auto-grow
                  v-model="link"
                  outlined
                ></v-text-field>
              </v-col>

              <div class="text-center">
                <v-btn class="" color="success" @click="addPost()">
                  <i class="mdi mdi-upload"></i> تحميل الأن
                </v-btn>
              </div>
            </div>
            <div
              v-if="showProg"
              class="text-center pa-3"
              style="margin-top:120px"
            >
              <h4>{{ textupload }}</h4>
              <!-- <v-progress-circular
                :rotate="360"
                :size="100"
                :width="15"
                :value="mm"
                color="teal"
              >
                {{ mm }}
              </v-progress-circular> -->
              <v-divider class="ma-4"></v-divider>
              <v-progress-linear rounded v-model="mm" height="25">
                <strong>{{ Math.ceil(mm) }}%</strong>
              </v-progress-linear>

              <v-divider class="ma-4"></v-divider>
              <v-btn v-if="trys" color="info" @click="tryagain()"
                ><i class="mdi mdi-plus"></i> إضافة فيديو جديد</v-btn
              >
            </div>
          </div>
          <v-divider class="ma-4"></v-divider>
        </v-card>
        <!-- nav left -->
        <!-- <NavLeft /> -->
      </div>
    </div>
  </div>
</template>

<script>
import NavRight from "@/components/pages/navright";
// import NavLeft from "@/components/pages/navleft";
export default {
  name: "Home",
  components: {
    NavRight,
    // NavLeft,
  },
  data() {
    return {
      title: "",
      desc: "",
      showProg: false,
      trys: false,
      mm: 0,
      textupload: "",
      knowledge: 100,
    };
  },
  methods: {
    tryagain() {
      this.showProg = false;
      this.mm = 0;
    },
    addPost: function() {
      this.textupload = "جاري تحميل الفيديو إنتضر من فضلك";
      this.showProg = true;
      if (this.link == "" || this.title == "") {
        this.$swal.fire("معدرة", "المرجو إضافة الفيديو وصورة معبرة", "error");
        this.showProg = false;
      } else {
        let formData = new FormData();
        formData.append("link", this.link);
        formData.append("desc", this.desc);
        formData.append("title", this.title);
        formData.append("userId", this.$store.getters.getUserId);
        const config = {
          //Choose upload
          onUploadProgress: (event) => {
            this.mm = Math.round((event.loaded * 100) / event.total);
            if (this.mm == 100) {
              this.trys = true;
            }
          },
          //Or download
          onDownloadProgress: (event) => {
            this.mm = Math.round((event.loaded * 100) / event.total);
            if (this.mm == 100) {
              this.trys = true;
            }
          },
          headers: {
            Accept: "application/json,*/*",
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Authorization",
            Authorization: "Bearer " + this.$store.getters.getToken,
          },
        };
        this.$http
          .post("/posts/add", formData, config)
          .then((res) => {
            this.$swal.fire("success", res.data.message, "success");
            // this.videoDownloadPercentage = 0;
            this.textupload =
              "تم تحميل الفيديو بنجاح  الأن في المراجعة من طرف التقنيين";
            this.thumbnail = "";
            this.video = "";
          })
          .catch((err) => {
            this.showProg = false;
            alert(err.message);

            // this.videoDownloadPercentage = 0;
          });
      }
    },
  },
};
</script>
